<template>
    <div>
        <div style="height: 50px;width: 100%;padding: 0px 17px;line-height: 50px;box-sizing: border-box">
            <span>选择日期范围查看：</span>
            <el-date-picker
                    size="small"
                    v-model="dateValue"
                    type="daterange"
                    align="right"
                    value-format="yyyy-MM-dd"
                    unlink-panels
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    :picker-options="pickerOptions">
            </el-date-picker>
            <el-button style="margin-left: 10px;" type="primary" size="small">搜索</el-button>
        </div>
        <div style="display: flex;align-content: space-around;margin: 0 10px">
            <PointAll id="point" :data="pointData" style="margin-right: 10px" />
            <DevAll id="devall" :data="devallData" />
        </div>
        <el-divider><i class="el-icon-s-data"></i></el-divider>
        <div style="width: 100%">
            <span style="margin-right: 10px;">搜索特定IP节点显示该设备的故障图表：</span>
            <el-select style="width: 300px;margin-right: 10px;" size="small" filterable  default-first-option v-model="value" placeholder="请选择搜索设备">
                <el-option-group
                        v-for="group in options"
                        :key="group.label"
                        :label="group.label">
                    <el-option
                            v-for="item in group.options"
                            :key="item.value"
                            :label="item.value.concat('-'+item.label)"
                            :value="item.value">
                        <span style="float: right">{{ item.label }}</span>
                        <span style="float: left; color: #8492a6; font-size: 13px">{{ item.value }}</span>
                    </el-option>
                </el-option-group>
            </el-select>
            <div style="display: flex;align-content: flex-start">
                <SingleDevFail id="Single" :data="SingleData" />
                <SingleTimeFail id="SingleTime" :data="SingleTimeData" />
            </div>
        </div>

    </div>
</template>

<script>
    import PointAll from "../../components/fai/PointAll";
    import DevAll from "../../components/fai/DevAll";
    import SingleDevFail from "../../components/fai/SingleDevFail";
    import SingleTimeFail from "../../components/fai/SingleTimeFail";
    export default {
        name: "FailureAll",
        components: {SingleDevFail, DevAll, PointAll,SingleTimeFail},
        data(){
            return {
                pointData:{},
                devallData:{},
                SingleData:{},
                SingleTimeData:{},
                dateValue:'',
                pickerOptions: {
                    shortcuts: [{
                        text: '最近一周',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近一个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近三个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                options: [{
                    label: '服务器',
                    options: [{
                        value: '172.17.137.85',
                        label: '应用服务器'
                    }, {
                        value: '172.17.137.31',
                        label: '项目运行服务器'
                    }]
                }, {
                    label: '网络设备',
                    options: [{
                        value: '172.17.137.115',
                        label: '测试设备路由器'
                    }, {
                        value: '172.17.137.126',
                        label: '长庚楼汇聚交换机'
                    }, {
                        value: '10.1.1.2',
                        label: '实验室内部测试设备'
                    }, {
                        value: '10.1.1.3',
                        label: '实验室内部测试设备'
                    }]
                }],
                value: ''
            }
        },
        methods:{
            initChart(){

            }
        },
        mounted() {
            this.initChart();
        }
    }
</script>

