<template>
    <div :id="id" :style="style"></div>
</template>

<script>
    export default {
        name: "SingleTimeFail",
        data(){
            return{
                option :{
                    title:{
                        subtext:'不同时间段故障发生次数',
                        top: '20'
                    },
                    legend: {},
                    tooltip: {},
                    dataset: {
                        source: [
                            ['product', '8:00-12:00', '12:00-16:00', '16:00:20:00', '20:00-24:00','24:00-4:00','4:00-8:00'],
                            ['SNMP连通性', 12, 34, 1, 22,12,23],
                            ['CPU利用率', 86, 92.1, 85.7, 83.1,11,32],
                            ['Ping连通性', 24, 67.2, 79.5, 86.4,22,33],
                            ['内存使用率', 24, 67.2, 79.5, 86.4,77,23],
                            ['OSPF', 24.1, 67.2, 79.5, 86.4,77,23],
                            ['端口状态', 34.1, 67.2, 79.5, 86.4,77,23],
                        ]
                    },
                    xAxis: [
                        {type: 'category', gridIndex: 0},
                    ],
                    yAxis: [
                        {gridIndex: 0},
                    ],
                    grid: [
                        {bottom: '10%'},
                        {top: '5%'}
                    ],
                    series: [
                        // These series are in the first grid.
                        {type: 'bar', seriesLayoutBy: 'row'},
                        {type: 'bar', seriesLayoutBy: 'row'},
                        {type: 'bar', seriesLayoutBy: 'row'},
                        {type: 'bar', seriesLayoutBy: 'row'},
                        {type: 'bar', seriesLayoutBy: 'row'},
                        {type: 'bar', seriesLayoutBy: 'row'},

                    ]
                },
                chart:'',
            }
        },
        computed: {
            style() {
                return {
                    height: this.height,
                    width: this.width
                }
            }
        },
        props:{
            id: {
                type: String
            },
            ip:{
                type:String
            },
            width: {
                type: String,
                default: "100%"
            },
            height: {
                type: String,
                default: "340px"
            },
            data: Object,
        },
        methods:{
            initSingleTime(){
                this.chart = this.$echarts.init(document.getElementById(this.id));
                this.chart.setOption(this.option,true);
                window.addEventListener("resize", this.chart.resize);
            }
        },
        mounted() {
            this.initSingleTime();
        },
    }
</script>

