<template>
    <div :id="id" :style="style"></div>
</template>

<script>
    export default {
        name: "PointAll",
        data(){
            return{
                option :{
                    title: {
                        text: '监测点出现次数',
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'shadow'
                        }
                    },
                    legend: {
                        data: ['设备数量']
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'value',
                        boundaryGap: [0, 0.01]
                    },
                    yAxis: {
                        type: 'category',
                        data: ['cpu利用率', '内存利用率', '磁盘使用率', '接口状态', 'ping连通性', 'snmp连通性','接口流量','远程登录','ospf','TRAP信息']
                    },
                    series: [
                        {
                            name: '设备数量',
                            type: 'bar',
                            data: [21, 12, 120, 15,21, 12, 120, 15,30,55],
                            color: ['#f49f42'],
                        },

                    ]
                },
                chart:'',
            }
        },
        computed: {
            style() {
                return {
                    height: this.height,
                    width: this.width
                }
            }
        },
        props:{
            id: {
                type: String
            },
            ip:{
                type:String
            },
            width: {
                type: String,
                default: "100%"
            },
            height: {
                type: String,
                default: "260px"
            },
            data: Object,
        },
        methods:{
            initPoint(){
                this.chart = this.$echarts.init(document.getElementById(this.id));
                this.chart.setOption(this.option,true);
                window.addEventListener("resize", this.chart.resize);
            }
        },
        mounted() {
            this.initPoint();
        },
    }
</script>

