<template>
    <div :id="id" :style="style"></div>
</template>

<script>
    export default {
        name: "SingleDevFail",
        data(){
            return{
                option :{
                    title: {
                        subtext: '该设备出现某种故障的次数',
                        left: 'center'
                    },
                    tooltip: {
                        trigger: 'item',
                        formatter: '{a} <br/>{b} : {c} ({d}%)'
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        data: ['PING连通性', 'cpu使用率', '内存使用率', 'SNMP连通性', 'OSPF','接口状态']
                    },
                    series: [
                        {
                            name: '访问来源',
                            type: 'pie',
                            radius: '55%',
                            center: ['50%', '60%'],
                            data: [
                                {value: 335, name: 'PING连通性'},
                                {value: 310, name: 'cpu使用率'},
                                {value: 234, name: '内存使用率'},
                                {value: 135, name: 'SNMP连通性'},
                                {value: 135, name: 'OSPF'},
                                {value: 1548, name: '接口状态'}
                            ],
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                },
                chart:'',
            }
        },
        computed: {
            style() {
                return {
                    height: this.height,
                    width: this.width
                }
            }
        },
        props:{
            id: {
                type: String
            },
            ip:{
                type:String
            },
            width: {
                type: String,
                default: "100%"
            },
            height: {
                type: String,
                default: "340px"
            },
            data: Object,
        },
        methods:{
            initSingle(){
                this.chart = this.$echarts.init(document.getElementById(this.id));
                this.chart.setOption(this.option,true);
                window.addEventListener("resize", this.chart.resize);
            }
        },
        mounted() {
            this.initSingle();
        },
    }
</script>

